.banner{
    
    background-size: cover;
    height: 560px;
    color: white;
    padding-top: 0.3rem;
}
.content{
    padding-top: 150px;
    height: 190px;
    padding-left: 15px;
    margin-bottom: 50px;
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(1, 0, 0, 0.5);
    cursor: pointer;
    margin-right: 1rem;

}
.button:hover{
    color: black;
    background-color: #e6e6e6;

}
.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 400px;
    
    
    

}
.fadebottom{
    height: 11rem;
    background-image: linear-gradient(180deg, transparent,rgba(37,37,37,.61),#111);
}
