.row{
    margin-left: 20px;
    color: #fff;
}
.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
    
}
.posters::-webkit-scrollbar{
    display: none;
}
.poster{

    max-height: 200px;
    margin-right: 10px;
    cursor: pointer;
}
.smallposter{
    
    max-height: 150px;
    cursor: pointer;
    margin-right: 10px;
}
.poster::-webkit-scrollbar{
    display: none;
}
.smallposter::-webkit-scrollbar{
    display: none;
}